@define-mixin container {
  padding-left: $small-grid-padding;
  padding-right: $small-grid-padding;
  max-width: 600px;
  margin: 0 auto;
  
  @media (min-width: $medium-breakpoint) {
    padding-left: $medium-grid-padding;
    padding-right: $medium-grid-padding;
    max-width: 1024px;
  }
  
  @media (min-width: $large-breakpoint) {
    max-width: 1440px;
  }
  
}

.row {
  display: flex;
  width: 100%;
  flex-direction: row;
  flex-wrap: wrap;
}

.small-column1 {
  box-sizing: border-box;
  width: 16.6667%;
  padding: 0 $grid-gutter;
}

.small-column2 {
  box-sizing: border-box;
  width: 33.3333%;
  padding: 0 $grid-gutter;
}

.small-column3 {
  box-sizing: border-box;
  width: 50%;
  padding: 0 $grid-gutter;
}

.small-column4 {
  box-sizing: border-box;
  width: 66.6667%;
  padding: 0 $grid-gutter;
} 

.small-column5 {
  box-sizing: border-box;
  width: 83.3333%;
  padding: 0 $grid-gutter;
}

.small-column6 {
  box-sizing: border-box;
  width: 100%;
  padding: 0 $grid-gutter;
}

@media (min-width: $medium-breakpoint) {
  
  .medium-column1 {
    box-sizing: border-box;
    width: 8.3333%;
    padding: 0 $grid-gutter;
  }
  
  .medium-column2 {
    box-sizing: border-box;
    width: 16.6667%;
    padding: 0 $grid-gutter;
  }
    
  .medium-column3 {
    box-sizing: border-box;
    width: 25%;
    padding: 0 $grid-gutter;
  }
  
  .medium-column4 {
    box-sizing: border-box;
    width: 33.3333%;
    padding: 0 $grid-gutter;
  }
  
  .medium-column5 {
    box-sizing: border-box;
    width: 41.6667%;
    padding: 0 $grid-gutter;
  }
  
  .medium-column6 {
    box-sizing: border-box;
    width: 50%;
    padding: 0 $grid-gutter;
  }
  
  .medium-column7 {
    box-sizing: border-box;
    width: 58.3333%;
    padding: 0 $grid-gutter;
  }
  
  .medium-column8 {
    box-sizing: border-box;
    width: 66.6667%;
    padding: 0 $grid-gutter;
  }
  
  .medium-column9 {
    box-sizing: border-box;
    width: 75%;
    padding: 0 $grid-gutter;
  }
  
  .medium-column10 {
    box-sizing: border-box;
    width: 83.3333%;
    padding: 0 $grid-gutter;
  }
  
  .medium-column11 {
    box-sizing: border-box;
    width: 91.6667%;
    padding: 0 $grid-gutter;
  }
  
  .medium-column12 {
    box-sizing: border-box;
    width: 100%;
    padding: 0 $grid-gutter;
  }
  
}

