/* stylelint-disable */
body,
html,
div,
blockquote,
img,
label,
p,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
ul,
ol,
li,
dl,
dt,
dd,
form,
a,
fieldset,
input,
th,
td {
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
}

html, body {
  line-height: 1;
  font-size: 16px;
  
  @media (min-width: $medium-breakpoint) {
    font-size: 20px;
  }
  
  @media (min-width: $large-breakpoint) {
    font-size: 24px;
  }
  
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  padding: 0;
  margin: 0;
}

ul,
ol {
  list-style: none;
}

a {
  color: black;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

/* stylelint-enable */